<script>
export default {
  name: 'Pikachu',
  data(){
    return{
      disX: 0,
      disY: 0
    }
  },
  methods: {
    moveBox(e){
      this.disX = e.clientX - this.$refs.pikachu.offsetLeft;
      this.disY = e.clientY - this.$refs.pikachu.offsetTop;
      document.onmousemove = event => {
        let left = event.clientX - this.disX;
        let top = event.clientY - this.disY;
        this.$refs.pikachu.style.left = left + 'px';
        this.$refs.pikachu.style.top = top + 'px';
      };
      document.onmouseup = () => {
        document.onmousemove = null;
        document.onmouseup = null;
      }
    }
  }
}
</script>
